}
.programs__image::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(49, 109, 194, 0.2);
  inset: 0;
  border-radius: 12px;
}

.programs__info {
  position: absolute;
  bottom: 0;
  padding: 20px 51px 21px 20px;
}
@media (max-width: 1199px) {
  .programs__info {
    padding: 20px 42px 21px 20px;
  }
}
@media (max-width: 767px) {
  .programs__info {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 17px 10px 18px 10px;
  }
}

.programs__info h3 {
  position: relative;
  font-size: 24px;
  font-weight: 400;
  line-height: 26px;
  color: #316dc2;
  background-color: #ffffff;
  padding: 14px 18px 3px 18px;
  margin: 0;
  margin-top: -1px;
  border-radius: 12px 12px 0 0;
  width: 60%;
  min-width: 194px;
}
.programs__info h3::after {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  bottom: 1px;
  right: -19px;
  background-image: url("../img/svg/angle-top.svg");
  background-repeat: no-repeat;
}
@media (max-width: 1199px) {
  .programs__info h3 {
    font-size: 20px;
    line-height: 24px;
    width: 66%;
    padding: 12px 18px 5px;
  }
}
@media (max-width: 767px) {
  .programs__info h3 {
    width: 54%;
    font-size: 16px;
    line-height: 18px;
    padding: 20px 18px 7px;
  }
}

.programs__description {
  display: flex;
  justify-content: space-between;
  background-color: #ffffff;
  padding: 9px 18px 16px 18px;
  border-radius: 0 12px 12px 12px;
  margin-top: -2px;
  align-items: center;
}
@media (max-width: 1199px) {
  .programs__description {
    padding: 7px 17px 10px 18px;
  }
  .programs__description--study {
    padding: 14px 17px 19px 18px;
  }
}
@media (max-width: 767px) {
  .programs__description {
    padding: 13px 18px 13px 18px;
  }
}

.programs__description p {
  font-size: 14px;
  line-height: 18px;
  margin: 0;
  width: 86%;
}
@media (max-width: 1199px) {
  .programs__description p {
    font-size: 14px;
    line-height: 20px;
    width: 80%;
  }
}
@media (max-width: 767px) {
  .programs__description p {
    font-size: 12px;
    line-height: 14px;
  }
}

.programs__button-arrow {
  position: relative;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #316dc2;
  display: block;
  transition: all 0.3s ease;
}
@media (max-width: 767px) {
  .programs__button-arrow {
    margin-top: -3px;
  }
}
.programs__button-arrow:hover, .programs__button-arrow:focus {
  background-color: #ffffff;
  outline: none;
}

.programs__button-arrow:hover svg,
.programs__button-arrow:focus svg {
  stroke: #316dc2;
  transform: rotate(45deg);
}

.programs__button-arrow:active svg {
  stroke: rgba(49, 109, 194, 0.7);
  transform: rotate(45deg);
}

.programs__button-arrow.is-disabled {
  background-color: #a9afb7;
  pointer-events: none;
}

.programs__button-arrow.is-disabled svg {
  stroke: rgba(255, 255, 255, 0.6);
}

.programs__button-arrow svg {
  position: absolute;
  top: 10px;
  right: 10px;
  stroke: #ffffff;
}

.programs__text {
  padding: 11px 10px 7px 0;
}
@media (max-width: 1199px) {
  .programs__text {
    padding: 0;
    width: 80%;
  }
}

.programs__controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 52px;
}

.programs__scrollbar {
  width: 100%;
  height: 2px;
  margin: 0 0 2px;
  background-color: rgba(49, 109, 194, 0.2);
}
@media (max-width: 767px) {
  .programs__scrollbar {
    display: none;
  }
}

.programs__scrollbar .swiper-scrollbar-drag {
  background-color: #316dc2;
}

.programs__buttons {
  display: flex;
  gap: 10px;
}
@media (max-width: 767px) {
  .programs__buttons {
    position: absolute;
    top: 0;
    right: 0;
  }
}

.programs__button {
  width: 22px;
  height: 22px;
  border: none;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0);
  cursor: pointer;
}
.programs__button:hover, .programs__button:focus {
  background-color: #ffffff;
  outline: none;
}
.programs__button:disabled {
  pointer-events: none;
}

.programs__button:active svg {
  stroke: rgba(49, 109, 194, 0.7);
}

.programs__button:disabled svg {
  stroke: #a9afb7;
}

.programs__button svg {
  stroke: #316dc2;
}

.programs__button--prev svg {
  transform: rotate(180deg);
}

.participation {
  min-height: 784px;
  background-color: #316dc2;
  color: #ffffff;
  padding: 96px 0 60px 0;
}
@media (max-width: 1199px) {
  .participation {
    padding: 58px 0 101px;
    min-height: 768px;
  }
}
@media (max-width: 767px) {
  .participation {
    padding: 59px 0 69px;
  }
}

.participation__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 498px);
  gap: 138px;
}
@media (max-width: 1199px) {
  .participation__wrapper {
    display: flex;
    flex-direction: column;
    gap: 18px;
  }
}

.participation__header h2 {
  color: #ffffff;
  margin: 40px 0 43px 0;
}
@media (max-width: 1199px) {
  .participation__header h2 {
    margin: 40px 0 27px 0;
  }
}
@media (max-width: 767px) {
  .participation__header h2 {
    margin: 9px 0;
  }
}

.participation__header p {
  font-weight: 400;
  line-height: 26px;
  margin-bottom: 33px;
}
@media (max-width: 1199px) {
  .participation__header p {
    line-height: 20px;
    width: 77%;
    margin-bottom: 36px;
  }
}
@media (max-width: 767px) {
  .participation__header p {
    width: 100%;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 25px;
  }
}

.participation__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 16px;
}

.participation__image {
  background-color: #275699;
  border-radius: 30px;
  min-height: 363px;
}
@media (max-width: 1199px) {
  .participation__image {
    min-height: 375px;
  }
}
@media (max-width: 767px) {
  .participation__image {
    min-height: 200px;
  }
}

.participation__description p {
  font-size: 32px;
  font-weight: 400;
  line-height: 36px;
  margin-bottom: 55px;
}
@media (max-width: 1199px) {
  .participation__description p {
    font-size: 28px;
    line-height: 28px;
    margin-bottom: 41px;
  }
}
@media (max-width: 767px) {
  .participation__description p {
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 25px;
  }
}

.participation__list {
  padding: 0;
  margin: 0;
  list-style: none;
  padding-left: 20px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}
@media (max-width: 1199px) {
  .participation__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 25px 49px;
  }
}
@media (max-width: 767px) {
  .participation__list {
    grid-template-columns: 1fr;
    padding: 0 0 0 15px;
    gap: 15px 0;
  }
}

.participation__item {
  position: relative;
  border: 1px solid #f0efe9;
  border-radius: 16px;
  font-weight: 400;
  line-height: 26px;
  padding: 23px 63px 21px 25px;
}
.participation__item::before {
  position: absolute;
  content: attr(data-number);
  width: 25px;
  height: 34px;
  background-color: #316dc2;
  font-size: 20px;
  top: -8px;
  left: -5px;
}
@media (max-width: 1199px) {
  .participation__item {
    padding: 25px 40px 25px 24px;
    line-height: 20px;
  }
  .participation__item::before {
    top: -5px;
    left: -6px;
  }
}
@media (max-width: 767px) {
  .participation__item {
    padding: 19px 28px 17px 24px;
    font-size: 14px;
    line-height: 20px;
  }
  .participation__item::before {
    top: -4px;
    left: -9px;
    width: 21px;
    height: 31px;
    padding: 5px;
    font-size: 18px;
  }
}

.feedback {
  min-height: 953px;
  background-color: #f0efe9;
  padding: 120px 0 30px 0;
}
@media (max-width: 1199px) {
  .feedback {
    padding: 99px 0 35px;
    min-height: 900px;
  }
}
@media (max-width: 767px) {
  .feedback {
    padding: 66px 0 15px;
    min-height: 836px;
  }
}

.feedback__wrapper {
  position: relative;
}

.feedback__form form {
  display: flex;
  flex-direction: column;
}
@media (max-width: 1199px) {
  .feedback__form form {
    margin-top: 14px;
  }
}

.feedback__content {
  position: relative;
  max-width: 498px;
  color: #ffffff;
  background-color: #316dc2;
  border-radius: 16px;
  padding: 70px 74px 50px 74px;
  display: flex;
  flex-direction: column;
  z-index: 1;
}
@media (max-width: 1199px) {
  .feedback__content {
    padding: 51px 44px 57px;
    max-width: 441px;
  }
}
@media (max-width: 767px) {
  .feedback__content {
    padding: 42px 15px 51px;
  }
}

.feedback__content h2 {
  font-size: 32px;
  font-weight: 400;
  line-height: 36px;
  margin-top: 11px;
}
@media (max-width: 1199px) {
  .feedback__content h2 {
    font-size: 28px;
    line-height: 28px;
    margin-bottom: 26px;
  }
}
@media (max-width: 767px) {
  .feedback__content h2 {
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 16px;
  }
}

.feedback__content p {
  line-height: 26px;
  color: rgba(240, 239, 233, 0.9);
  margin: 0;
  margin-bottom: 2px;
}
@media (max-width: 1199px) {
  .feedback__content p {
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .feedback__content p {
    font-size: 14px;
    margin: 0 0 4px;
  }
}

.feedback__image {
  position: absolute;
  inset: 0;
  background-color: #275699;
  border-radius: 16px;
}

.feedback__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 16px;
}

.feedback__button {
  padding: 20px 71px 20px 20px;
  font-size: 14px;
  font-weight: 500;
  line-height: 14px;
  letter-spacing: 0.28px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #316dc2;
  border: 1px solid #ffffff;
  transition: all 0.3s ease;
}
.feedback__button::after {
  right: 4px;
  background-color: #ffffff;
}
@media (max-width: 1199px) {
  .feedback__button {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .feedback__button {
    padding: 13px 68px 13px 30px;
    font-size: 12px;
    line-height: 12px;
    letter-spacing: 0.24px;
  }
  .feedback__button::after {
    right: 3px;
  }
}
.feedback__button:hover, .feedback__button:focus {
  background-color: #ffffff;
  color: #316dc2;
  outline: none;
}
.feedback__button:hover::after, .feedback__button:focus::after {
  background-color: #316dc2;
}
.feedback__button:active {
  background-color: #ffffff;
  color: rgba(49, 109, 194, 0.7);
}
.feedback__button:active::after {
  background-color: #316dc2;
}

.feedback__button:hover .feedback__button-icon,
.feedback__button:focus .feedback__button-icon {
  stroke: #ffffff;
  transform: rotate(45deg);
}

.feedback__button:disabled .feedback__button-icon {
  stroke: rgba(255, 255, 255, 0.7);
}

.feedback__button-icon {
  position: absolute;
  right: 20px;
  z-index: 1;
  stroke: #316dc2;
}
@media (max-width: 767px) {
  .feedback__button-icon {
    right: 14px;
  }
}

@media (max-width: 767px) {
  .feedback__button svg {
    right: 14px;
  }
}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  background: rgba(13, 29, 51, 0.4);
  z-index: 2;
  display: none;
}

.overlay.is-opened {
  display: block;
}

